.footer {
  position: relative;
  z-index: 1;
  background: #010E0F;
  padding: 3.125rem 0;
}
.footer-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 4.375rem;
    margin-left: 2rem;
  }
  &__logo {
    width: 9.0625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    color: #D9D9D9;
    margin-left: 1.5rem;
    span {
      display: block;
    }
  }
  &__location {
    font-size: 0.875rem;
    line-height: 140%;
    span {
      display: block;
      color: #767676;
    }
    p {
      display: block;
      color: #ffffff;
      margin: 0.3125rem 0 0 0;
    }
  }
  &__phone2 {
    font-size: 0.875rem;
    line-height: 140%;
    color: #ffffff !important;
    margin-top: 1rem;
    &:hover {
      color: #F0D683 !important;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }
  &__social {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: radial-gradient(119.38% 119.38% at 10.4% 8.42%, #3D3D3D 0%, #141414 100%);
    border-radius: 0.5rem;
    padding: 0.16875000000000004rem;
    &:hover {
      background: #3D3D3D;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
  }
  &__phone {
    display: block;
    font-family: 'Oranienbaum';
    font-size: 1.25rem;
    line-height: 130%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #ffffff !important;
    &:hover {
      color: #F0D683 !important;
    }
  }
  &__btn {
    justify-content: flex-end;
    background: transparent;
    margin-top: 0.625rem;
    span {
      font-size: 1rem;
      line-height: 130%;
      color: #F0D683;
      border-bottom: 0.0625rem dashed #F0D683;
      padding-bottom: 0.5rem;
    }
    &:hover {
      span {
        background-image: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        border-color: transparent;
      }
    }
  }
}
.footer-bottom {
  color: #ffffff;
  margin-top: 2rem;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  &__logo {
    width: 11.875rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__info {
    font-size: 0.875rem;
    line-height: 140%;
    p {
      margin: 0;
    }
    a {
      color: #F0D683;
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    gap: 0.625rem;
    p {
      margin: 0;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__right {
      gap: 2rem;
    }
    &__socials {
      gap: 1rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .footer-top {
    &__right {
      gap: 1.5rem;
      margin-left: 1.5rem;
    }
    &__socials {
      gap: 0.75rem;
    }
    &__phone {
      font-size: 1.125rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer {
    padding: 2rem 0;
  }
  .footer-top {
    &__inner {
      display: block;
    }
    &__right {
      margin: 2rem 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__left {
      flex-direction: column;
      text-align: center;
    }
    &__right {
      flex-direction: column;
      text-align: center;
    }
    &__text {
      margin: 1rem 0 0 0;
    }
  }
  .footer-bottom {
    &__inner {
      flex-direction: column;
      text-align: center;
    }
    &__left {
      flex-direction: column;
    }
    &__text {
      flex-direction: column;
    }
  }
}