.btn-brown {
  height: 3.4375rem;
  background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
  border-radius: 0.3125rem;
  span {
    font-family: 'Oranienbaum';
    font-size: 1rem;
    line-height: 130%;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    color: #ffffff;
  }
  &:hover {
    background: #F0D683;
  }
}
h2 {
  font-size: 2.5rem;
  line-height: 130%;
}
.line {
  position: absolute;
  pointer-events: none;
  span:nth-child(1) {
    display: block;
    border: 0.125rem solid rgba(240, 214, 131, 0.50);
    width: 46.25rem;
    height: 25rem;
  }
  span:nth-child(2) {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    display: block;
    border: 0.125rem solid rgba(240, 214, 131, 0.50);
    width: 46.25rem;
    height: 31.25rem;
  }
}
.select__wrap {
  position: relative;
}
.select__wrap--active {
  .select__list-default {
    background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
    .select__current {
      color: #ffffff;
    }
    .select-arrow {
      transform: rotate(-180deg);
    }
    .select-arrow svg path {
      fill: #ffffff;
    }
  }
  .select__list {
    transform: translateY(0);
    opacity: 1;
    pointer-events: all;
  }
}
.select__list-default {
  height: 3.125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border-radius: 62.5rem;
  padding: 0 2rem;
  cursor: pointer;
  &:hover {
    background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
    .select__current {
      color: #ffffff;
    }
    .select-arrow svg path {
      fill: #ffffff;
    }
  }
}
.select__current {
  font-family: 'Oranienbaum';
  font-size: 1.5rem;
  line-height: 130%;
  color: #000;
}
.select-arrow {
  flex-shrink: 0;
  width: 1rem;
  margin-left: 1rem;
  svg {
    width: 100%;
  }
}
.select__list {
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 0;
  transform: translateY(-2rem);
  width: 15.3125rem;
  padding-top: 0.625rem;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    margin-top: 1.375rem;
  }
  ul li {
    position: relative;
    font-size: 1rem;
    line-height: 130%;
    color: #000;
    cursor: pointer;
    padding-left: 1.5rem;
  }
  li:after {
    content: '';
    position: absolute;
    top: 0.1875rem;
    left: 0;
    width: 0.9375rem;
    height: 0.9375rem;
    border: 0.0625rem solid #C3C3C3;
    border-radius: 50%;
    transition: all 0.3s;
  }
  li:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0.375rem;
    left: 0.1875rem;
    width: 0.5625rem;
    height: 0.5625rem;
    background: #67310A;
    border-radius: 50%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
}
.select__list-content {
  background: #ffffff;
  border: 0.0625rem solid #DFDFDF;
  box-shadow: 0 0.0625rem 0.1875rem 0 rgba(0, 0, 0, 0.10), 0px 5px 5px 0px rgba(0, 0, 0, 0.09), 0px 11px 7px 0px rgba(0, 0, 0, 0.05), 0px 20px 8px 0px rgba(0, 0, 0, 0.01), 0px 31px 9px 0px rgba(0, 0, 0, 0.00);
  border-radius: 0.875rem;
  padding: 1rem 1.5rem 1.5rem 1.5rem;
}
.select-search input {
  width: 100%;
  height: 2.375rem;
  background: #F3F3F3;
  border-color: transparent;
  border-radius: 46.25rem;
  padding: 0 1.5rem;
  font-size: 1rem;
  line-height: 130%;
  color: #000000;
  &::placeholder {
    color: #858585;
  }
}
.li--active {
  &:after {
    background: #F3F3F3;
    border-color: #F3F3F3 !important;
  }
  &:before {
    opacity: 1 !important;
    pointer-events: all !important;
  }
}
.form-group {
  border: none;
  padding: 0;
  margin: 0;
  input {
    width: 100%;
    height: 3.4375rem;
    background: #F3F3F3;
    border-color: transparent;
    border-radius: 0.5rem;
    padding: 0 1.25rem;
    font-size: 1rem;
    line-height: 130%;
    color: #000000;
    &::placeholder {
      color: #898484 !important;
    }
  }
}
.form {
  .agreement-check {
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      font-size: 0.8125rem;
      line-height: 140%;
      color: #ffffff;
      padding-left: 2.5rem;
    }
    label:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1.875rem;
      height: 1.875rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 0.8125rem 0.375rem;
      border: 0.0625rem solid #CB9B4A;
      border-radius: 0.5625rem;
    }
    label a:hover {
      color: #CB9B4A;
    }
  }
}
.button {
  display: flex;
  justify-content: center;
}
.navigation {
  padding: 3.125rem 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    font-size: 1.5rem;
    line-height: 140%;
    color: #000000;
    a:hover {
      color: #cb9b4a;
    }
  }
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2.25rem;
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 2rem;
  }
  .line {
    span:nth-child(1) {
      width: 32rem;
      height: 16rem;
    }
    span:nth-child(2) {
      width: 32rem;
      height: 22rem;
    }
  }
}