html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: 'NotoSans', sans-serif;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1815px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Oranienbaum';
  font-weight: 400;
  margin: 0;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    min-width: auto;
    padding: 0;
  }
}
img {
  object-fit: cover;
}