.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  background: #010E0F;
  padding: 1.5625rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 2rem;
  }
  &__logo {
    width: 9.0625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    color: #D9D9D9;
    margin-left: 1.5rem;
    span {
      display: block;
    }
  }
  &__email {
    font-size: 0.875rem;
    line-height: 140%;
    span {
      display: block;
      color: #767676;
    }
    a {
      display: block;
      color: #F0D683;
      border-bottom: 0.0625rem dashed #F0D683;
      padding-bottom: 0.5rem;
      transition: unset;
    }
    a:hover {
      background-image: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      border-color: transparent;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 1.25rem;
    margin-left: 4.375rem;
  }
  &__social {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: radial-gradient(119.38% 119.38% at 10.4% 8.42%, #3D3D3D 0%, #141414 100%);
    border-radius: 0.5rem;
    padding: 0.16875000000000004rem;
    &:hover {
      background: #3D3D3D;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
    margin-left: 4.375rem;
  }
  &__phone {
    display: block;
    font-family: 'Oranienbaum';
    font-size: 1.25rem;
    line-height: 130%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #ffffff !important;
    &:hover {
      color: #F0D683 !important;
    }
  }
  &__btn {
    justify-content: flex-end;
    background: transparent;
    margin-top: 0.625rem;
    span {
      font-size: 1rem;
      line-height: 130%;
      color: #F0D683;
      border-bottom: 0.0625rem dashed #F0D683;
      padding-bottom: 0.5rem;
    }
    &:hover {
      span {
        background-image: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        border-color: transparent;
      }
    }
  }
}
.burger {
  position: relative;
  z-index: 5;
  width: 2rem;
  height: 1.5rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: #ffffff;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.625rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.625rem;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1399px) {
  .header {
    &__socials {
      gap: 1rem;
      margin-left: 2rem;
    }
    &__group {
      margin-left: 2rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .header {
    &__left {
      position: relative;
      z-index: 5;
    }
    &__right {
      position: fixed;
      z-index: 4;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      text-align: center;
      background: #010E0F;
      padding: 9.375rem 1.5rem 6.25rem;
      margin: 0;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
    }
    &__text {
      font-size: 0.875rem;
    }
    &__socials {
      margin: 1.5rem 0 0 0;
    }
    &__group {
      margin: 1.5rem 0 0 0;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 499px) {
  .header {
    &__text {
      display: none;
    }
  }
}

