.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: right center;
  background-size: cover;
  padding: 17.5rem 0 5.625rem;
  &__inner {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 40%;
      left: -30rem;
      transform: translate(0, -50%) rotate(-12.448deg);
      width: 75rem;
      height: 75rem;
      background: rgba(255, 255, 255, 0.95);
      filter: blur(6.25rem);
      pointer-events: none;
    }
  }
  &__wrap {
    position: relative;
    z-index: 1;
    max-width: 40rem;
  }
  h1 {
    font-size: 4rem;
    line-height: 120%;
    letter-spacing: -0.64px;
    color: #000;
  }
  &__text {
    font-size: 1.75rem;
    line-height: 130%;
    letter-spacing: -0.28px;
    margin-top: 2rem;
  }
  &__group {
    display: flex;
    gap: 2.375rem;
    margin-top: 5.625rem;
  }
  &__btn {
    width: 15.3125rem;
  }
  &__info {
    position: relative;
    max-width: 17.5rem;
    p {
      font-size: 0.875rem;
      line-height: 130%;
      letter-spacing: -0.28px;
      color: #000000;
      margin: 0;
    }
    i {
      position: absolute;
      top: -100%;
      left: -3.75rem;
      width: 5.25rem;
      img:nth-child(2) {
        display: none;
      }
    }
    i img {
      width: 100%;
    }
  }
  &__items {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    margin-top: 5.625rem;
  }
  &__item {
    max-width: 29.6875rem;
    flex-grow: 1;
    flex-basis: 0;
    display: flex;
    align-items: center;
    background: #FFF;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius: 0.875rem;
    padding: 3.125rem;
    &-icon {
      flex-shrink: 0;
      max-width: 6.5rem;
      margin-right: 2rem;
    }
    &-text {
      font-size: 1.25rem;
      line-height: 130%;
      letter-spacing: -0.8px;
      color: #1D1C1C;
    }
  }
}
.cars {
  padding: 0 0 3.125rem 0;
  &__inner {
    position: relative;
    z-index: 1;
  }
  h2 {
    text-align: center;
  }
  p {
    text-align: center;
    font-size: 1.5rem;
    line-height: 130%;
    margin: 1rem 0 0 0;
  }
  &-filter {
    position: relative;
    padding: 3.125rem 0 6.5625rem 0;
    margin-top: 1.25rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: calc(100% + 7.5rem);
      height: 100%;
      background: #F3F3F3;
      border-radius: 0.875rem;
      pointer-events: none;
    }
  }
  &-checkboxes {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    border-bottom: 0.0625rem solid #67310A;
    padding-bottom: 2.8125rem;
  }
  &-checkbox {
    position: relative;
    max-width: 16.5625rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      &:after {
        border-color: #67310A;
      }
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      position: relative;
      font-family: 'Oranienbaum';
      font-size: 1.5rem;
      line-height: 130%;
      padding-left: 1.5rem;
      margin-top: 1rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.3125rem;
        left: 0;
        width: 1.0625rem;
        height: 1.0625rem;
        border: 0.125rem solid #444343;;
        border-radius: 50%;
        transition: all 0.3s;
      }
      &:before {
        content: '';
        position: absolute;
        top: 0.5625rem;
        left: 0.25rem;
        width: 0.5625rem;
        height: 0.5625rem;
        background: #67310A;
        border-radius: 50%;
        transition: all 0.3s;
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  &-checkbox__img {
    max-width: 16.5625rem;
    margin-bottom: auto;
  }
  &-selections {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 0.625rem 1.875rem;
    margin-top: 2.8125rem;
  }
  &-select__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.625rem 1.875rem;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 1.5rem / 3));
    gap: 1.5rem;
    margin-top: -3.125rem;
  }
  .line {
    right: -22rem;
    bottom: -9.375rem;
  }
}
.car-card {
  position: relative;
  min-height: 15.625rem;
  border-radius: 0.625rem;
  color: #ffffff;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 2.25rem);
    height: calc(100% - 2.25rem);
    border: 0.0625rem solid #ffffff;
    border-radius: 0.3125rem;
    pointer-events: none;
  }
  &__img {
    position: relative;
    height: 100%;
    padding-bottom: 50%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 0.625rem;
    }
  }
  &__desc {
    position: absolute;
    top: 2.5rem;
    left: 2.5rem;
    right: 2.5rem;
    bottom: 2.5rem;
  }
  &__title {
    font-family: 'Oranienbaum';
    font-size: 1.5rem;
    line-height: 130%;
  }
  &__price {
    font-size: 1.125rem;
    line-height: 130%;
    margin-top: 0.625rem;
  }
}
.advantages {
  padding: 3.125rem 0;
  &__inner {
    position: relative;
    z-index: 1;
  }
  h2 {
    max-width: 45.625rem;
    text-align: center;
    margin: 0 auto;
  }
  &__cards {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    margin-top: 7.5rem;
  }
  &__group {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1rem;
    margin-top: 3.125rem;
  }
  &__btn {
    width: 9.375rem;
  }
  &__info {
    position: relative;
    max-width: 13.125rem;
    p {
      font-family: 'Oranienbaum';
      font-size: 0.875rem;
      line-height: 130%;
      color: #000;
      margin: 0;
    }
    i {
      position: absolute;
      top: 0.875rem;
      left: 100%;
      width: 2.625rem;
    }
    i img {
      width: 100%;
    }
  }
  .line:nth-child(1) {
    top: 13.75rem;
    left: -5rem;
    span:nth-child(2) {
      right: -3.125rem;
    }
  }
  .line:nth-child(2) {
    right: -6.25rem;
    bottom: 10.125rem;
    span:nth-child(2) {
      left: -3.125rem;
    }
  }

}
.advantage-card {
  max-width: 28.75rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  &__img {
    position: relative;
    z-index: 1;
    max-width: 18.4375rem;
    margin: 0 auto;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% + 2rem);
      height: calc(100% + 2rem);
      background: #ffffff;
      border-radius: 50%;
    }
    img {
      position: relative;
      z-index: 1;
    }
    img:nth-child(1) {
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      max-width: 130%;
      max-height: unset;
    }
  }
  &__desc {
    position: relative;
    flex-grow: 1;
    background: #393939;
    border-radius: 0.625rem;
    padding: 5.625rem 1.875rem 3.125rem;
    color: #ffffff;
    margin-top: -1rem;
  }
  &__title {
    font-size: 1.25rem;
    line-height: 130%;
  }
  &__number {
    position: absolute;
    top: 2.8125rem;
    right: 1.875rem;
    font-family: 'Oranienbaum';
    font-size: 6rem;
    line-height: 100%;
    opacity: 0.2;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 1rem;
    li {
      position: relative;
      font-size: 1.125rem;
      line-height: 130%;
      padding-left: 0.625rem;
    }
    li:after {
      content: '';
      position: absolute;
      top: 0.625rem;
      left: 0;
      width: 0.25rem;
      height: 0.25rem;
      background: #ffffff;
      border-radius: 50%;
    }
  }
}
.steps {
  padding: 5rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    padding-right: max(6rem, calc(100% - 1623px) / 2);
  }
  &__left {
    position: relative;
    width: calc(100% - 58.75rem);
  }
  &__right {
    width: 52.5rem;
    margin-left: 6.25rem;
  }
  &__img {
    position: relative;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      right: -9.375rem;
      bottom: -5rem;
    }
  }
  h2 {
    .text_color {
      background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 6.25rem 4rem;
    margin-top: 4.375rem;
  }
  &__item {
    position: relative;
    max-width: 16.25rem;
    &-img {
      width: 100%;
      height: 10rem;
    }
    &-img img {
      position: relative;
      z-index: 1;
    }
    &-number {
      position: absolute;
      top: 0rem;
      right: 0;
      font-size: 6.875rem;
      line-height: 100%;
      font-weight: 700;
      background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &-desc {
      margin-top: 1rem;
    }
    &-text {
      font-size: 1.25rem;
      line-height: 140%;
      color: #000;
      strong {
        display: block;
        font-weight: 700;
      }
    }
  }
  .line {
    width: 100%;
    top: -3.125rem;
    left: 2rem;
    span {
      width: 100% !important;
    }
    span:nth-child(2) {
      right: -3.125rem;
    }
  }
}
.rent {
  padding: 0;
  margin: 5rem 0;
  &__inner {
    position: relative;
    background-image: url("../img/rent-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 0.875rem;
    padding: 3.4375rem 4.25rem;
  }
  &__desc {
    max-width: 42.8125rem;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: -4.6875rem;
    max-width: 51.25rem;
    img {
      width: 100%;
    }
  }
  h2 {
    color: #ffffff;
  }
  p {
    font-size: 1.5rem;
    line-height: 130%;
    color: #ffffff;
    margin: 1rem 0 0 0;
  }
  &-form {
    margin-top: 1.5rem;
  }
  .form_block {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    gap: 1.5rem;
  }
  .select__wrap--active {
    .select__list-default {
      background: #F3F3F3;
      .select__current {
        color: #898484;
      }
      .select-arrow {
        transform: rotate(0);
      }
      .select-arrow svg path {
        fill: #ADADAD;
      }
    }
  }
  .select__list-default {
    width: 100%;
    height: 3.4375rem;
    justify-content: space-between;
    background: #F3F3F3;
    border-radius: 0.5rem;
    padding: 0 1.25rem;
    &:hover {
      background: #F3F3F3;
      .select__current {
        color: #898484;
      }
      .select-arrow svg path {
        fill: #ADADAD;
      }
    }
  }
  .select__list {
    left: unset;
    right: 0;
  }
  .select__list-content {
    height: 17.5rem;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 0.375rem;
    }
    &::-webkit-scrollbar-track {
      background: #F0EDED;
      border-radius: 0.25rem;
    }
    &::-webkit-scrollbar-thumb {
      background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
      border-radius: 0.25rem;
    }
  }
  .select__current {
    font-family: 'NotoSans';
    font-size: 1rem;
    line-height: 130%;
    color: #898484
  }
  .calendar {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 1.25rem;
    }
    span img {
      width: 100%;
    }
  }
  &-form__btn {
    width: 13.125rem;
    grid-column: 1/3;
  }
  .line {
    left: 3.125rem;
    bottom: -3.125rem;
    span:nth-child(2) {
      right: -3.125rem;
    }
  }
}
.advantages2 {
  padding: 5rem 0 3.125rem;
  h2 {
    text-align: center;
