.feedbacks {
  background-image: url("../img/feedbacks-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 15.625rem 0 0 0;
  &__inner {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 48.4375rem;
    margin-right: 1rem;
  }
  &__right {
    width: calc(100% - 49.4375rem);
    margin-top: -6.25rem;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1.5rem;
    padding-right: max(6rem, calc(100% - 791px) / 2);
  }
  h2 {
    max-width: 35.3125rem;
  }
  &__slider-nav {
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
  .slick-arrow {
    position: static;
    width: 4rem;
    height: 4rem;
    border: 0.0625rem solid #393939;
    box-shadow: unset;
    &:hover {
      background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
      border-color: transparent;
      svg path {
        fill: #ffffff;
      }
    }
    svg {
      width: 0.9375rem;
      height: auto;
    }
  }
  &__slider {
    margin-top: 2.8125rem;
  }
  .slick-list {
    margin: 0 -1rem;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: auto;
    margin: 0 1rem;
    & > div {
      height: 100%;
    }
  }
  .slides-numbers {
    display: flex;
    align-items: center;
    font-family: 'Oranienbaum';
    font-size: 2.75rem;
    line-height: 100%;
    color: #393939;
  }
}
.feedback-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #393939;
  border-radius: 0.875rem;
  padding: 3.125rem 2rem;
  &__title {
    font-size: 1.125rem;
    line-height: 140%;
    font-weight: 700;
    color: #ffffff;
  }
  &__text {
    font-size: 1.125rem;
    line-height: 140%;
    color: #F3F3F3;
    margin-top: 2.8125rem;
    margin-bottom: auto;
  }
  &__link {
    position: relative;
    display: inline-block;
    font-size: 1rem;
    line-height: 130%;
    background: linear-gradient(90deg, #67310A -108.98%, #F0D683 129.77%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding-bottom: 0.625rem;
    margin-top: 2rem;
    &:after {
      content: '';
      position: absolute;
