    }
    &__items {
      margin-top: 3rem;
    }
  }
  .cars {
    &-checkboxes {
      grid-template-columns: repeat(2, calc(50% - 2rem /2));
    }
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .advantages {
    &__cards {
      grid-template-columns: 100%;
    }
    .line {
      display: none;
    }
  }
  .advantage-card {
    max-width: 100%;
  }
  .steps {
    &__items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 2rem / 2));
      gap: 2rem;
    }
    &__item {
      max-width: 100%;
    }
  }
  .rent {
    .container-fluid {
      padding: 0;
    }
    &__inner {
      padding: 2rem 2rem 15rem 2rem;
    }
  }
  .advantages2 {
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      flex-direction: row;
    }
  }
  .director {
    &__inner {
      max-width: 100%;
    }
    &__img {
      img:nth-child(2) {
        right: -8rem;
        width: 17rem;
      }
      img:nth-child(3) {
        display: none;
      }
    }
    &__desc {
      padding-left: 1.5rem;
    }
    .quote {
      font-size: 0.875rem;
    }
  }
}

@media screen and (max-width: 499px) {
  .main {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .steps {
    &__inner {
      padding: 0 1.5rem;
    }
  }
  .rent {
    &__inner {
      padding: 2rem 1.5rem 15rem 1.5rem;
    }
    .calendar {
      grid-template-columns: 100%;
      gap: 1rem;
      span {
        display: none;
      }
    }
    &-form__btn {
      width: 100%;
    }
  }
  .advantages2 {
    &__wrap {
      margin-top: 2rem;
    }
    &__img {
      width: 18.75rem;
      height: 18.75rem;
      &:after {
        width: 20rem;
        height: 20rem;
      }
      &:before {
        width: 21.25rem;
        height: 21.25rem;
      }
    }
  }
  .feedbacks {
    &__inner {
      padding: 0 1.5rem;
    }
  }
}

@media screen and (max-width: 399px) {
  .steps {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .director {
    &__img {
      img:nth-child(2) {
        right: -6rem;
        width: 14rem;
      }
    }
    &__info {
      padding: 0.5rem 1rem;
      strong {
        font-size: 0.75rem;
      }
      p {
        font-size: 0.75rem;
      }
    }
  }
  .gratitude {
    padding-bottom: 15.625rem;
    .director__img {
      img:nth-child(2) {
        bottom: -15rem;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .steps {
    &__inner {
      padding: 0 1rem;
    }
  }
  .feedbacks {
    &__inner {
      padding: 0 1rem;
    }
  }
}